<template>
  <div class="course-record-screen-question">
    <head-top title="截屏提问列表">
    </head-top>

    <van-pull-refresh v-model="isLoading" @refresh="onSearch" class="pull_fresh">
      <div v-if="questionList && questionList.length> 0" class="flex-wrap-between list-main">
        <div v-for="(sub, k) in questionList" :key="k" class="image_card van-hairline--surround"
             @click="questionSelect(sub)">
          <div class="list-item">
            <r-image width="40vw" height="28vw" :src="sub.imgSrc" class="item-left">
            </r-image>
            <div class="item-right">
              <div class="flex-between">
                <span>序号：{{ sub.index }}</span>
                <van-tag mark plain>{{sub.stateName}}</van-tag>
              </div>
              <div class="flex-between">
                <span>计时：{{sub.time}}</span>
                <van-tag plain type="success">{{sub.ModeName}}</van-tag>
              </div>
              <div class="flex-between">
                <span>分数：{{sub.score}}</span>
                <span>{{sub.createTime}}</span>
              </div>
              <div></div>
            </div>
          </div>
        </div>
        <span style="width: 40vw;"></span>
      </div>
      <van-cell v-else title="暂无数据"></van-cell>
    </van-pull-refresh>

    <div class="footer">
      <van-tabbar v-model="selectIndex">
        <van-tabbar-item v-for="(item, index) in typeOptions" :key="index" @click="handleClick">
          <i slot="icon" class=" iconfont" :class="item.icon"></i>
          <span>{{item.typeName}}</span>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import RImage from "@/components/r-image";
import { ModulePicture, screenPatternName, ScreenSubjectStateName } from "@/utils/enum";
import { formatDate } from "@/utils/formatData";

export default {
  name: "screenQuestion",
  components: { RImage },
  data () {
    return {
      courseID: this.$store.getters.courseID,
      courseInfo: this.$store.getters.course,

      isLoading: false,
      dataList: [],
      typeOptions: [
        { typeName: "单选题", icon: "icon-danxuanti", typeValue: 1, questionList: [] },
        { typeName: "多选题", icon: "icon-duoxuanti", typeValue: 2, questionList: [] },
        { typeName: "判断题", icon: "icon-panduanti", typeValue: 3, questionList: [] },
        { typeName: "问答题", icon: "icon-wendati", typeValue: 4, questionList: [] }
      ],
      selectIndex: (this.$route.params.type || 1) - 1,
      typeNames: ['单选题', '多选题', '判断题', '问答题'],
      questionModeNames: ['全员作答', '随机挑人', '抢答']
    };
  },
  computed: {
    questionList () {
      return this.typeOptions[this.selectIndex].questionList;
    }
  },
  mounted () {
    this.typeChanged();
  },
  methods: {
    handleClick () {
      this.$router.replace({
        params: {
          type: (this.selectIndex + 1)
        }
      });
      this.typeChanged();
    },
    typeChanged () {
      let subject = this.typeOptions[this.selectIndex];
      subject.questionList = [];
      this.isLoading = true;
      this.$mainHttp.screenQuestion.dataList({
        ciID: this.courseID,
        ssType: subject.typeValue
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["CreateTime"] > b["CreateTime"] ? 1 : -1;
            });

            res.Datas.forEach(item => {
              subject.questionList.push({
                id: item["SSID"],
                type: subject.typeValue,
                imgSrc: this.$mainHttp.resourse.picture(item["SSContent"], ModulePicture.ScreenSubject),
                state: item["State"],
                stateName: ScreenSubjectStateName(item["State"]),
                time: item["SSTime"],
                score: item["SSScore"],
                optionsNum: item["SSOption"],
                questionMode: item["SSPattern"],
                ModeName: screenPatternName(item["SSPattern"]),
                index: item["SSSerial"],
                answer: item["SSAnswer"],
                survey: item["SSSurvey"],
                createTime: formatDate(item["CreateTime"], "MM-dd hh:mm")
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    onSearch () {
      this.typeChanged();
    },
    questionSelect (sub) {
      this.$store.commit("SET_SCREEN_QUESTION", sub);
      this.$router.push("/course/screen-question/view");
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";

.course-record-screen-question {
  min-height: 100%;
  background-color: #fff;

  .footer {
    /deep/ .van-tabbar-item--active {
      color: #00cbab;
    }
  }

  .image_card {
    width: 100%;
  }

  .list-main {
    padding-top: 10px;
    margin-left: 10px;
    margin-bottom: 44px;
  }

  .list-item {
    display: flex;
    align-items: center;
    width: 100%;

    .item-left {
      flex: 1;
    }

    .item-right {
      flex: 3;
      margin: 0 16px;
      font-size: @font-info;

      .flex-between {
        line-height: 2;
      }
    }
  }
}

</style>
